import React, { useEffect, useState } from "react";
import { getAuth } from "firebase/auth";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../state/store";
import List from "../components/Trip Container/Create Trip/CreateTrip";
import TripItem from "../components/Trip Container/TripItem/TripItem";
import { AppDispatch } from "../state/store";
import { fetchTripsData } from "../state/slicers/dashboardSlice";
import DeleteTrip from "../components/Trip Container/Edit Trip/DeleteTrip";

const DashBoard = () => {
  const auth = getAuth();
  const dispatch = useDispatch<AppDispatch>();

  // Local state
  // const [loading, setLoading] = useState(true);
  const [createNewListBox, setCreateNewListBox] = useState(false);

  // states
  const trip = useSelector(
    (state: RootState) => state.dashboardReducer.tripsData
  );
  const loader = useSelector(
    (state: RootState) => state.dashboardReducer.loading
  );
  const userId = useSelector((state: RootState) => state.loginReducer.userId);

  useEffect(() => {
    if (userId) {
      dispatch(fetchTripsData(userId));
    }
  }, [userId, dispatch]);
  // console.log(trip, 'this is trip')

  return (
    <>
      {/* checking state to load lightbox */}
      {createNewListBox && <List onClose={() => setCreateNewListBox(false)} />}
      {userId ? <button
      className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
      onClick={() => setCreateNewListBox(true)}
      >
        Create Trip
      </button> :
      <p>Please, login</p>
}
      {!loader &&
        trip?.length > 0 &&
        trip.map((trip: any, index: number) => {
          return (
            <div key={trip.tripId}>
              <div>
                <h1 className="text-3xl font-bold mb-4">{trip.tripName}</h1>
                <div className="text-center text-gray-400">
                  <DeleteTrip tripId={trip.tripId} userId={userId} />
                </div>
              </div>

            
              <TripItem
                key={trip.tripId} // Use a unique identifier from the data itself
                trip={trip.itineraries}
                tripId={trip.tripId}
                userId={userId}
              />
            </div>
          );
        })}
      
    </>
  );
};

export default DashBoard;
