import React, { useEffect, useState } from "react";
import "./Navbar.css";
import { useSelector } from "react-redux";
import { RootState } from "../../state/store";
import { getAuth, signOut } from "firebase/auth"; // Import getAuth and signOut
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { getUserId } from "../../state/slicers/loginSlice";
import { Link, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import { emptyDashboard } from "../../state/slicers/dashboardSlice";
import { resetFlightData } from "../../state/slicers/flightSlice";
import { resetSearch } from "../../state/slicers/searchSlice";
import { onAuthStateChanged, User } from "firebase/auth";


export const Navbar = () => {
  const [activeTab, setActiveTab] = useState("search");

  const userId = useSelector((state: RootState) => state.loginReducer.userId);
  const flightItem = useSelector(
    (state: RootState) => state.flightReducer.flights
  );
  
  const auth = getAuth(); // Initialize auth
  const dispatch = useDispatch();
  const navigate = useNavigate(); // Initialize navigate
  const location = useLocation();



  // Local state
  const [loading, setLoading] = useState(true);

  // auth state change
  const handleAuthStateChange = (userId: User | null) => {
    if (userId) {
      dispatch(getUserId(userId.uid));
    }
    setLoading(false);
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, handleAuthStateChange);
    return () => {
      unsubscribe();
    };
  }, [auth]);

  const updateActiveTab = () => {
    const currentPath = location.pathname;
    switch (currentPath) {
      case "/":
        setActiveTab("search");
        break;
      case "/dashboard":
        setActiveTab("dashboard");
        dispatch(resetFlightData());
        dispatch(resetSearch());
        break;
    }
  };

  useEffect(() => {
    updateActiveTab();
  }, [location]);

  // Updated handleLogout function
  const handleLogout = () => {
    signOut(auth).then(() => {
      dispatch(getUserId(""));
      navigate("/");
      dispatch(emptyDashboard());
      toast.success("Logged out successfully!", {
        position: "top-center",
        autoClose: 3000, // The toast message will be shown for 3 seconds
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: false,
      });
    });
  };

  const isLoginPage = location.pathname === "/login";

  return (
    <>
      {!isLoginPage && (
        <nav className="flex items-center justify-between flex-wrap bg-white p-6">
          <div className="flex items-center flex-shrink-0 text-gray-800">
            <Link
              className="text-xl font-semibold tracking-tight site-title mr-2"
              to="/"
              onClick={() => setActiveTab("search")}
            >
              Flyplan
            </Link>
          </div>
          <div className="flex">
            <div>
              {userId ? (
                <button
                  onClick={handleLogout}
                  className="inline-block px-4 py-2 leading-none text-white bg-gray-800 rounded hover:bg-gray-700 lg:mt-0"
                >
                  Logout
                </button>
              ) : (
                <Link
                  to="/login"
                  className="inline-block px-4 py-2 leading-none text-white bg-gray-800 rounded hover:bg-gray-700 lg:mt-0"
                >
                  Login
                </Link>
              )}
            </div>
          </div>
        </nav>
      )}
      {!isLoginPage && (
        <div className="text-sm font-medium text-center text-gray-500 border-b border-gray-200 dark:text-gray-400 dark:border-gray-700">
          <ul className="flex flex-wrap -mb-px">
            <li className="mr-2">
              <Link
                to="/"
                className={`inline-block p-4 rounded-t-lg hover:text-gray-600 hover:border-gray-300 dark:hover:text-gray-300 ${
                  activeTab === "search"
                    ? "text-blue-600 border-blue-600 border-b-2 active dark:text-blue-500 dark:border-blue-500"
                    : "border-b-2 border-transparent"
                }`}
                onClick={() => setActiveTab("search")}
              >
                Search
              </Link>
            </li>
            <li className="mr-2">
              <Link
                to="/dashboard"
                className={`inline-block p-4 rounded-t-lg hover:text-gray-600 hover:border-gray-300 dark:hover:text-gray-300 ${
                  activeTab === "dashboard"
                    ? "text-blue-600 border-blue-600 border-b-2 active dark:text-blue-500 dark:border-blue-500"
                    : "border-b-2 border-transparent"
                }`}
                onClick={() => setActiveTab("dashboard")}
              >
                Dashboard
              </Link>
            </li>
          </ul>
        </div>
      )}
    </>
  );
};
