import React, { useEffect } from "react";
import {
  getAuth,
  GoogleAuthProvider,
  signInWithPopup,
  onAuthStateChanged,
  User,
} from "firebase/auth";
import { useNavigate } from "react-router-dom";
import { RootState } from "../state/store";
import { useSelector, useDispatch } from "react-redux";
import { toggleAuth, getUserId } from "../state/slicers/loginSlice";
import "./Login.css";

export interface ILoginPageProps {}

export const Login = (props: ILoginPageProps) => {
  const auth = getAuth();
  const navigate = useNavigate();

  const dispatch = useDispatch();
  // const userId = useSelector((state: RootState) => state.loginReducer.userId);

  const handleAuthStateChange = (user: User | null) => {
    if (user) {
      dispatch(toggleAuth(true));
      navigate("/dashboard");
    } else {
      dispatch(toggleAuth(false));
    }
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, handleAuthStateChange);
    return () => {
      unsubscribe();
    };
  }, [auth, dispatch, navigate]);

  const signInWithGoogle = async () => {
    signInWithPopup(auth, new GoogleAuthProvider())
      .then((response) => {
        navigate("/");
        dispatch(getUserId(auth.currentUser?.uid!));
      })
      .catch((error) => {
        console.log(error);
        dispatch(toggleAuth(false));
      });
  };

  return (
    <div className="min-h-screen bg-gray-100 flex items-center justify-center">
      <div className="max-w-md w-full bg-white shadow-lg rounded-lg px-10 py-8 space-y-6">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-900">
            Welcome to Flyplan
          </h2>
        </div>
        <div>
          <button
            type="submit"
            className="w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium  bg-blue-600 rounded-md text-white hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
            onClick={() => signInWithGoogle()}
          >
            Sign in with Google
          </button>
        </div>
        <div className="text-center text-gray-400">
          <span>New to Flyplan? </span>
          <a href="#" className="text-blue-500 font-bold">
            Create an account
          </a>
        </div>
        <div className="text-center text-gray-400">
          <button onClick={() => navigate("/")}>Cancel</button>
        </div>
      </div>
    </div>
  );
};
