import React, { useEffect, useState, ChangeEvent } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { RootState } from '../../state/store'
import { getAutoSuggest } from '../../helpers/autoSuggest';
import { getFlightInfo, toggleOneWay, setSendClicked} from '../../state/slicers/searchSlice';
import { setInputAutoDataOrigin, setInputAutoDataDestination, setSelectedValueOrigin, setSelectedValueDestination, setAirportList} from '../../state/slicers/autosuggestSlice';
import { IOptionAirportSelect } from '../../types';
import AsyncSelect from 'react-select/async';

const Search = () => {
  const dispatch = useDispatch()
  //Global states
  const oneWayState = useSelector((state: RootState) => state.searchReducer.oneWay)
  const selectedOriginState = useSelector((state: RootState) => state.autosuggestReducer.selectedValueOrigin)
  const airportList = useSelector((state: RootState) => state.autosuggestReducer.airportList)
  const selectedDestinationState = useSelector((state: RootState) => state.autosuggestReducer.selectedValueDestination)

  // Local State input departure time
  const [departureDate, setDepartureDate] = useState<string>(new Date().toISOString().slice(0, 10));

  useEffect(() => {
    const getData = async () => {
      const data = await getAutoSuggest()
      dispatch(setAirportList(data))
    }
    getData()
  }, [])
  
  const getFlightData = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData(event.target as HTMLFormElement);
    const origin = selectedOriginState;
    const destination = selectedDestinationState;
    const departureDate = formData.get("departure-date") as string;
    const returnDate = formData.get("return-date") as string;
    const oneWay = oneWayState;
    dispatch(getFlightInfo({
      origin,
      destination,
      departureDate,
      returnDate,
      oneWay,
      sendClicked: true
    }));
  
    // dispatch the setSendClicked action after dispatching the getFlightInfo action
    dispatch(setSendClicked(true));
  };
  
  const handleDepartureDateChange = (e: ChangeEvent<HTMLInputElement>) => {
    setDepartureDate(e.target.value)
  }

const filterCities = async (inputValue: string, isOrigin: boolean) => {
  const data: IOptionAirportSelect[] = await inputValue.length > 1 ? airportList.filter((item: IOptionAirportSelect) => item.label.toLowerCase().includes(inputValue.toLowerCase())) : [];
  if (isOrigin) {
    dispatch(setInputAutoDataOrigin(data));
  } else {
    dispatch(setInputAutoDataDestination(data));
  }
  return data;
};

  return (
    <>
      <section className="bg-white rounded-md shadow-md p-6">
        <form className="flex flex-col space-y-4" onSubmit={getFlightData}>
          <div className="flex space-x-4">
            <label className="flex flex-col flex-1">
              <span className="text-gray-600 font-medium mb-2">From</span>
              <AsyncSelect
                placeholder='Origin'
                // defaultOptions
                loadOptions={(inputValue: string) => filterCities(inputValue, true)}
                onChange={(selectedOption: any) => dispatch(setSelectedValueOrigin(selectedOption?.value))}
              />
            </label>
            <label className="flex flex-col flex-1">
              <span className="text-gray-600 font-medium mb-2">To</span>
              <AsyncSelect
                placeholder='Destination'
                // defaultOptions
                loadOptions={(inputValue: string) => filterCities(inputValue, false)}
                onChange={(selectedOption: any) => dispatch(setSelectedValueDestination(selectedOption?.value))}
              />
            </label>
          </div>
          <div className="flex space-x-4">
            <label className="flex flex-col flex-1">
              <span className="text-gray-600 font-medium mb-2">Departure Date</span>
              <input
                type="date"
                name="departure-date"
                className="border border-gray-400 rounded-md px-4 py-2 focus:outline-none focus:border-blue-500"
                min={new Date().toISOString().slice(0, 10)}
                onChange={handleDepartureDateChange}
              />
            </label>
            {!oneWayState && (
              <label className="flex flex-col flex-1">
                <span className="text-gray-600 font-medium mb-2">Return Date</span>
                <input
                  type="date"
                  name="return-date"
                  className="border border-gray-400 rounded-md px-4 py-2 focus:outline-none focus:border-blue-500"
                  min={departureDate}
                />
              </label>
            )} 
          </div>
          <div className="flex justify-between">
            <div className="flex items-center space-x-4">
              <span className="text-gray-600 font-medium">One Way Flight</span>
              <div
                className={`${
                  oneWayState ? 'bg-blue-500' : 'bg-gray-400'
                } relative inline-flex items-center h-6 rounded-full w-11`}
                onClick={() => dispatch(toggleOneWay(oneWayState))}
              >
                <span className="sr-only">Toggle One Way</span>
                <span
                  className={`${
                    oneWayState ? 'translate-x-6' : 'translate-x-1'
                  } inline-block w-4 h-4 transform bg-white rounded-full`}
                />
              </div>
            </div>
            <button type='submit' className="bg-blue-500 hover:bg-aqua-700 rounded-md px-4 py-2 text-white focus:outline-none">
              Search
            </button>
          </div>
        </form>
      </section>
      </>
      );
};

export default Search;
